import { ButtonGroup, Button, Menu, MenuItem, Fade } from "@mui/material";
import React from "react";
import { useState } from "react";

const LinksData = [
  {
    name: "GOOGLE",
  },
  {
    name: "LOPENLING",
  },
  { name: "BDRC" },
];
function LinkSelection(props) {
  const handleClick = ({ type, name }) => {
    switch (type) {
      case "GOOGLE":
        window.open(`https://www.google.com/search?q=${name}`);
        break;
      case "LOPENLING":
        window.open("https://lopenling.org");
        break;
      case "BDRC":
        break;
      default:
        break;
    }
  };

  return (
    <>
      <ButtonGroup
        disableElevation
        sx={{
          gap: 1,
          display: {
            xs: "none",
            md: "flex",
          },
          justifyContent: "center",
        }}
        variant="contained"
        className="Links"
      >
        {LinksData.map((link, i) => (
          <Button
            key={`linkdatas-${i}`}
            variant="outlined"
            className="ViewButton"
            onClick={() =>
              handleClick({
                type: link.name,
                name: props.item.name,
              })
            }
          >
            {link.name}
          </Button>
        ))}
      </ButtonGroup>
      <ForMobile {...props} />
    </>
  );
}

export default LinkSelection;

const ForMobile = (props) => {
  const [anchorEl, setAnchorEl] = useState();
  const open = Boolean(anchorEl);
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = ({ type, name }) => {
    setAnchorEl(null);
    switch (type) {
      case "GOOGLE":
        window.open(`https://www.google.com/search?q=${name}`);
        break;
      case "LOPENLING":
        window.open("https://lopenling.org");
        break;
      case "BDRC":
        break;
      default:
        break;
    }
  };

  return (
    <>
      <Button
        id="fade-button"
        aria-controls={open ? "fade-menu" : undefined}
        aria-haspopup="true"
        aria-expanded={open ? "true" : undefined}
        onClick={handleClick}
        sx={{
          display: {
            xs: "block",
            md: "none",
          },
          margin: "0 auto",
        }}
      >
        links
      </Button>
      <Menu
        id="fade-menu"
        MenuListProps={{
          "aria-labelledby": "fade-button",
        }}
        anchorEl={anchorEl}
        open={open}
        onClose={handleClose}
        TransitionComponent={Fade}
      >
        {LinksData.map((link, i) => {
          return (
            <MenuItem
              key={link.name + i}
              onClick={() =>
                handleClose({ type: link.name, name: props.item.name })
              }
            >
              {link.name}
            </MenuItem>
          );
        })}
      </Menu>
    </>
  );
};
