import { data } from "../helper/pandetaInfo";

const initialState = {
  List: data,
  selectedIndex: 0,
  loaded: false,
};
export const reducer = (state = initialState, action) => {
  switch (action.type) {
    case "CHANGE_SELECTION":
      return {
        ...state,
        selectedIndex: action.data,
      };
    case "changeLoaded":
      return {
        ...state,
        loaded: action.data,
      };
    default:
      return {
        ...state,
      };
  }
};
