import React, { useState, useRef, useEffect, useMemo } from "react";
import { useSelector, useDispatch } from "react-redux";
import { Grid, Paper, Container, Select, MenuItem } from "@mui/material";
import { styled } from "@mui/material/styles";

function Namecards() {
  const dispatch = useDispatch();
  const setSelectedIndex = (data) =>
    dispatch({ type: "CHANGE_SELECTION", data });
  const data = useSelector((data) => data.List);
  const selectedIndex = useSelector((data) => data.selectedIndex);

  const [isVisible, setIsVisible] = useState(false);
  const targetRef = useRef();
  const handleSelection = (e) => {
    const num = parseInt(e.target.value);

    setSelectedIndex(num - 1);
  };

  const callback = (entries) => {
    const [entry] = entries;
    setIsVisible(entry.isIntersecting);
  };

  const options = useMemo(() => {
    return {
      root: null,
      rootMargin: "0px",
      threshold: 0.3,
    };
  });

  useEffect(() => {
    const observer = new IntersectionObserver(callback, options);
    const currentTarget = targetRef.current;
    if (currentTarget) observer.observe(currentTarget);

    return () => {
      if (currentTarget) observer.unobserve(currentTarget);
    };
  }, []);

  const handleClick = (id) => {
    setSelectedIndex(parseInt(id));
  };
  return (
    <>
      <Container
        sx={{
          marginTop: 2,
          display: { md: "block", xs: "none" },
          transition: "1s all ease",
          opacity: !isVisible ? "0" : "1",
        }}
        ref={targetRef}
      >
        <Grid container spacing={2}>
          {data.map((pan, index) => (
            <Grid item md={3} key={pan.id} onClick={() => handleClick(index)}>
              <Item
                sx={{
                  background:
                    index === selectedIndex
                      ? "rgb(7, 177, 77, 0.42)"
                      : "inherit",
                }}
              >
                {pan.name}
              </Item>
            </Grid>
          ))}
        </Grid>
      </Container>
      <Container sx={{ marginTop: 2, display: { md: "none", xs: "block" } }}>
        <label htmlFor="selection"> Select Pendeta : </label>
        <Select
          id="selection"
          onChange={handleSelection}
          value={selectedIndex + 1}
        >
          {data.map((pan, index) => (
            <MenuItem md={2} key={pan.id} value={pan.id}>
              {pan.name}
            </MenuItem>
          ))}
        </Select>
      </Container>
    </>
  );
}

const Item = styled(Paper)(({ theme }) => ({
  backgroundColor: theme.palette.mode === "dark" ? "#1A2027" : "#fff",
  ...theme.typography.body2,
  padding: theme.spacing(1),
  textAlign: "center",
  color: theme.palette.text.secondary,
  cursor: "pointer",
  "&:hover": {
    backgroundColor: "rgb(7, 177, 77, 0.42)",
  },
  fontWeight: "bold",
  fontSize: 19,
}));

export default React.memo(Namecards);
