import React from "react";
import "./index.css";
import { Box, Typography, Button, Tooltip } from "@mui/material";
import { Container } from "@mui/system";
import { Link } from "react-scroll";
import Zoom from "react-img-zoom";
import Thangka from "../../img/thangka.png";
function index() {
  return (
    <Box
      sx={{
        overflow: "hidden",
        height: "100vh",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        flexDirection: "column",
      }}
    >
      <div style={{ cursor: "zoom-in" }}>
        <Zoom
          img={Thangka}
          // alt="nalanda "
          // id="floated"
          zoomScale={3}
          width={500}
          height={300}
        />
      </div>

      <Typography
        sx={{ alignSelf: { md: "center", xs: "left" } }}
        className="home-heading"
        variant="h4"
        my={3}
      >
        The Seventeen Panditas of Nalanda Monastery
      </Typography>
      <Container
        sx={{
          display: { md: "block", xs: "flex" },
          flexDirection: "column-reverse",
        }}
      >
        {/* <Box m={2} mt={3}> */}
        {/* <Tooltip title="Thangka image depicting Shakaymuni Buddha and 17 Nalanda Panditas"> */}

        {/* </Tooltip> */}
        {/* </Box> */}
        <Box
          sx={{
            display: "flex",
            flexDirection: "column",
            justifyContent: "center",
            height: "100%",
          }}
        >
          <Typography
            sx={{
              fontsize: { xs: 1, md: 3 },
              display: "inline",
            }}
            variant="subtitle1"
          >
            The Seventeen Pandits of Nalanda Monastery refers to a grouping of
            seventeen of the most important and influential Mahayana Buddhist
            masters from Indias past. His Holiness the Dalai Lama frequently
            refers to himself as a follower of the lineage of the seventeen
            Nalanda masters today.
          </Typography>
          <Box
            sx={{ display: "flex", gap: 4, justifyContent: "flex-start" }}
            mt={3}
          >
            <Link
              to="Pandetas"
              smooth={true}
              spy={true}
              offset={-64}
              duration={500}
            >
              <Button variant="contained" color="info" size="large">
                Pendatas
              </Button>
            </Link>
            <Link
              to="Contact"
              smooth={true}
              spy={true}
              offset={-64}
              duration={500}
            >
              <Button variant="text" color="secondary">
                Contact
              </Button>
            </Link>
          </Box>
        </Box>
      </Container>
    </Box>
  );
}

export default index;
